import React from 'react';
import ColumnComponent from './default-components/column-component';
import RowComponent from './default-components/row-component';

export default class Table extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <table>

        <tr>
          {this.props.columns.map(column => {
            return <ColumnComponent column={column}/>
          })}
        </tr>

        {this.props.data.map(rowData => {
          return (<RowComponent columns={this.props.columns} row={rowData}/>)
        })}

      </table>);
  }
}

Table.propTypes = {
  pippo: React.PropTypes.string.isRequired,
  columns: React.PropTypes.object.isRequired,
  // data: React.PropTypes.array.isRequired,
  data: function (props, propName, componentName) {

    console.log("ENTER HERE");

    if (!/matchme/.test(props[propName])) {
      return new Error(
        `Invalid prop ${propName} supplied to
        ${componentName}. Validation failed`);
    }

    if (Object.prototype.toString.call(props[propName]) !== '[object Array]') {
      return new Error(
        `Invalid prop ${propName} type, it must be an array`);
    }

    let columns = props['columns'];
    props[propName].forEach(dataRow => {
      for (let col in dataRow) {

        let found = false;
        console.log("CIAO");
        for (let i = 0; i < columns.length && !found; i++) {
          found = columns.hasOwnProperty(col);
        }

        if (!found) {
          console.log("CIAO");
          return new Error(`Invalid column ${col} in data`);
        }
      }
    });

  }.isRequired,

};

